import React from 'react';
import { Box, Typography, Checkbox, Avatar } from '@mui/material';
import AvatarIcon from '../../../../assets/ImageJaven/Avatar.png';
import '../../../Styles/View_activity_style.css';
import useStyle from '../../../Styles/View_activity_style';
import axios from 'axios';
import { useParams } from 'react-router-dom';
import Score from './Score';

function StudentList({
  setSubmitData,
  setStudentID,
  score,
  activityView,
  submitData,
  scores,
}) {
  const { designs } = useStyle();
  const { roomID, activityID } = useParams();
  const [data, setData] = React.useState(null);
  React.useEffect(() => {
    axios
      .post('https://murmuring-basin-16459.herokuapp.com/rooms/my-room', {
        roomID,
      })
      .then((res) => {
        axios
          .post('https://murmuring-basin-16459.herokuapp.com/get/members', {
            members: res.data.members,
          })
          .then((res) => setData(res.data))
          .catch((err) => console.log(err));
      })
      .catch((error) => console.log(error));
  }, []);
  return (
    <>
      {data &&
        data.map(function (items, index) {
          if (items.userType === 'Student') {
            return (
              <Box
                key={index}
                sx={designs.Student_Box_Style}
                onClick={() => {
                  setStudentID(items._id);
                  axios
                    .post(
                      'https://murmuring-basin-16459.herokuapp.com/activity/get/submit',
                      {
                        activityID,
                        stdID: items._id,
                      }
                    )
                    .then((res) => {
                      setSubmitData(res.data.activity);
                      console.log(res.data.activity);
                    })
                    .catch((err) => console.log(err));
                  document.querySelector('#activityScore').value =
                    score[items._id] || 0;
                }}
              >
                {submitData && submitData?.[0]?.activityStatus !== 'Graded' && (
                  <Checkbox
                    sx={designs.Student_Checkbox_Style}
                    onChange={(e) => {
                      if (e.target.checked === true) {
                        scores.current.push({
                          stdID: items._id,
                          score: score[items._id] || 0,
                        });

                        console.log(
                          scores.current.filter((value) => {
                            return value.stdID;
                          })
                        );
                      }
                      if (e.target.checked === false) {
                        scores.current = scores.current.filter((value) => {
                          return value.stdID !== items._id;
                        });
                      }
                    }}
                  />
                )}
                <Avatar
                  alt="Remy Sharp"
                  src={items.image}
                  sx={designs.Student_Avatar_Style}
                />
                <Typography
                  noWrap
                  sx={{
                    height: 'max-content',
                    width: '47%',
                    textTransform: 'Capitalize',
                    textAlign: 'Left',
                    fontSize: '13px',
                    fontWeight: '500',
                    color: '#3F3D56',
                    margin: '10px 0px 10px 0px',
                  }}
                >
                  {items.firstName} {items.lastName}
                </Typography>
                <Box flexGrow={1} height="relative" width="relative" />

                <Score
                  submitData={submitData}
                  score={score}
                  activityView={activityView}
                  items={items}
                  designs={designs}
                />
              </Box>
            );
          }
        })}
    </>
  );
}

export default StudentList;
